<template>
    <div>
        <b-card
            title="Work Type"
            sub-title="Edit work type"
            align="left"
            v-show="worktype.title != ''"
        >

            <b-form @submit.prevent="update()">
                <div class="form-group">
                    <label>Title</label>
                    <b-form-input type="text" v-model="worktype.title" required :disabled="!worktype.title"/>
                </div>

                <div class="form-group">
                    <label>Description</label>
                    <b-form-textarea v-model="worktype.description" :disabled="!worktype.description"></b-form-textarea>
                </div>

                <div class="form-group">

                    <b-button @click="remove()" v-if="!loadingDelete" size="sm" variant="danger" class="pull-left">REMOVE</b-button>
                    <!-- Show when deleting... -->
                    <b-button size="sm" v-if="loadingDelete" variant="danger" class="pull-left" disabled>Removing...</b-button>

                    <b-button type="submit" v-if="!loadingUpdate" size="sm" variant="info" class="pull-right">UPDATE</b-button>
                    <!-- Show when updating... -->
                    <b-button size="sm" disabled v-if="loadingUpdate" variant="info" class="pull-right">Updating...</b-button>

                </div>

            </b-form>

        </b-card>
    </div>
</template>

<script>
import { mapActions } from 'vuex';
export default {
    props: {
        worktype: {
            type: Object,
            default: () => {
                return {};
            }
        },
    },
    data() {
        return {
            loadingUpdate: false,
            loadingDelete: false,
        }
    },
    methods: {
        ...mapActions(['FETCH_WORKTYPES','REMOVE_WORKTYPE', 'UPDATE_WORKTYPE']),

        remove() {
            let conf = confirm('Are you sure you want to remove "'+this.worktype.title+'"?');
            
            if(conf) {
                this.loadingDelete = true;

                this.REMOVE_WORKTYPE(JSON.stringify(this.worktype))
                    .then( res => {

                        if(res.success) {
                            this.notify('success', res.success);
                            this.loadingDelete = false;
                            this.FETCH_WORKTYPES()
                                .then( () => {
                                    this.worktype.title = '';
                                    this.worktype.description = '';
                                })
                            
                        } else {
                            this.notify('danger', res);
                            this.loadingDelete = false;
                        }

                    })
            }
        },
        update() {
            this.loadingUpdate = true;

            this.UPDATE_WORKTYPE(JSON.stringify(this.worktype))
                .then( res => {

                    if(res.success) {
                        this.notify('success', res.success);
                        this.FETCH_WORKTYPES();
                        this.loadingUpdate = false;        
                    } else {
                        this.notify('danger', res.message);
                        this.loadingUpdate = false;
                    }
                })
        },
        notify(type, msg) {
            this.$notify({
                verticalAlign: 'top',
                horizontalAlign: 'right',
                type: type,
                message: msg
            });
        }
    }
}
</script>

