<template>
    <div>
        <div>
            <card>
                <h4 class="title">{{ title }}</h4>
                <h6 class="card-subtitle text-muted">{{ subtitle }}</h6>

                <b-table :busy="isBusy" :items="data" :fields="columns" :per-page="perPage" :current-page="currentPage">

                    <template v-slot:cell(projectName)="data">
                        {{ data.item.project.name }}
                    </template>

                    <template v-slot:cell(n_size)="data">
                        <b-form @submit.prevent="update_job(data.item.id, 'n_size', $event)">
                            <b-form-input size="sm" name="n_size" :value="data.item.n_size" class="readonly text-center"></b-form-input>
                        </b-form>
                    </template>

                    <template v-slot:cell(num_of_resp)="data">
                        <b-form @submit.prevent="update_job(data.item.id, 'num_of_resp', $event)">
                            <b-form-input size="sm" name="num_of_resp" :value="data.item.num_of_resp" class="readonly text-center"></b-form-input>
                        </b-form>
                    </template>

                    <template v-slot:cell(num_of_client_users)="data">
                        <b-form @submit.prevent="update_job(data.item.id, 'num_of_client_users', $event)">
                            <b-form-input size="sm" name="num_of_client_users" :value="data.item.num_of_client_users" class="readonly text-center"></b-form-input>
                        </b-form>
                    </template>

                    <template v-slot:cell(todos)="row">
                        <b-button size="sm" variant="secondary" @click="row.toggleDetails" class="text-secondary">
                            <!-- <span v-show="row.detailsShowing" class="tim-icons icon-minimal-up"></span>
                            <span v-show="!row.detailsShowing" class="tim-icons icon-minimal-down"></span> -->
                            {{ row.detailsShowing ? '-':'+' }}
                        </b-button>
                    </template>

                    <template v-slot:cell(owner_image)="data")>
                        <img :src="data.value" class="photo"/>
                    </template>

                    <template v-slot:cell(status)="data">
                        <span v-if="data.value == 'for_approval'" class="badge badge-primary">For Approval</span>
                        <span v-if="data.value == 'approved'" class="badge badge-success">Approved</span>
                        <span v-if="data.value == 'in_queue'" class="badge badge-warning">In Queue</span>
                        <span v-if="data.value == 'cancelled'" class="badge badge-warning">Cancelled</span>
                        <span v-if="data.value == 'in_process'" class="badge badge-info">Processing</span>
                        <span v-if="data.value == 'completed'" class="badge badge-info">Completed</span>
                    </template>

                    <template v-slot:cell(action)="data">
                        <b-button v-if="btn_start && data.item.status == 'approved'" @click="updateStatus(data.item.id, 'in_process', data.item)" class="btn btn-sm btn-info">Start</b-button>
                        <b-button v-if="btn_complete" @click="updateStatus(data.item.id, 'completed', data.item)" class="btn btn-sm btn-success">Complete</b-button>
                        <b-button v-if="btn_cancel && data.item.status != 'cancelled'" @click="updateStatus(data.item.id, 'cancelled', data.item)" class="btn btn-sm btn-warning">CANCEL</b-button>
                        <b-button v-if="btn_forApproval && data.item.status == 'cancelled'" @click="updateStatus(data.item.id, 'for_approval', data.item)" class="btn btn-sm btn-primary">For Approval</b-button>
                    </template>

                    <!-- Loading -->
                    <template v-slot:table-busy>
                        <div class="text-center text-danger my-2">
                        <b-spinner class="align-middle"></b-spinner>
                        <strong>Loading...</strong>
                        </div>
                    </template>

                    <!-- List of todos -->
                    <template v-slot:row-details="row">
                        <b-card title="TODOS" sub-title="List of todos" type="primary">
                            
                            <!-- Todo form -->
                            <b-form @submit.prevent="submit_todo(row.item.id, $event)">
                                <b-form-group label-align="center">
                                    <b-form-input
                                        type="text"
                                        name="todo_title"
                                        placeholder="Please input todo and press enter"
                                        required
                                    />
                                </b-form-group>
                            </b-form>

                            <!-- Todo table -->
                            <b-table 
                                :current-page="todo_currentPage" 
                                :per-page="todo_perPage"  
                                :items="filter_todos(row.item.id)" 
                                :fields="todo_columns"
                                >

                                <template v-slot:cell(todo_title)="data">
                                    <b-form @submit.prevent="update_todo(data.item.id, $event)">
                                        <b-form-input name="todo_title" size="sm" title="Click and press enter to update" class="readonly" :value="data.item.todo_title" required/>
                                    </b-form>
                                </template>

                                <template v-slot:cell(action)="data">
                                    <!-- Remove Button -->
                                    <b-button 
                                        title="remove" 
                                        size="sm" 
                                        variant="danger"
                                        @click="remove_todo(data.item)"
                                    >
                                        <i class="tim-icons icon-trash-simple"></i>
                                    </b-button>

                                </template>
                            </b-table>

                            <!-- todo table pagination -->
                            <b-pagination v-if="filter_todos(row.item.id).length > todo_perPage" v-model="todo_currentPage" :total-rows="filter_todos(row.item.id).length" :per-page="todo_perPage" align="center"></b-pagination>

                        </b-card>
                    </template>

                </b-table>

            <b-pagination v-if="data.length > perPage" v-model="currentPage" :total-rows="data.length" :per-page="perPage" align="center"></b-pagination>

            </card>
        </div>
    </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
export default {
    name: 'UserBasetable',
    props: {
        'title': String,
        'subtitle': String,
        'data': Array,
        'todos': Array,
        'columns': Array,
        'btn_start': Boolean,
        'btn_complete': Boolean,
        'btn_cancel': Boolean,
        'btn_forApproval': Boolean,
        'isBusy': Boolean,
    },
    data() {
        return {
            perPage: 5,
            todo_perPage: 3,
            currentPage: 1,
            todo_currentPage: 1,
            dbData: [],
            hasRecord: false,
            recordLength: 0,
            form: {
                job_id: null,
                todo_title: ''
            },
            todo_columns: [
                {
                    key: "todo_title",
                    label: "name"
                },
                'action'
            ],
            dummy_todos: [
                {
                    "id": "1",
                    "name": "Dummy",
                    "nodes": "0",
                    "n_size": "0",
                    "num_of_resp": "0",
                    "num_of_client_users": "0",
                    "status": "dummy_status",
                    "owner_id": "0",
                    "owner_name": "Niel Malitoc",
                    "owner_image": "http://localhost/ejm/api/images/default-avatar.png",
                    "owner_roles": "role_User,role_Admin",
                    "created_at": "Mon Jan. 20, 2020 09:01 PM"
                }
            ],
            mail: {
                to: '',
                from: '',
                subject: '',
                message: '',
            },
            current_item_status: null,
        }
    },
    created() {
        this.FETCH_ALL_ADMINS();
    },
    computed: {
        ...mapGetters([
            'admin_users'
        ]),
    },
    methods: {
        ...mapActions([
            'fetchJobs',
            'updateTask', 
            'ADD_TODO', 
            'FETCH_TODOS', 
            'DELETE_TODO',
            'UPDATE_TODO',
            'SEND_MAIL',
            'FETCH_ALL_ADMINS',
        ]),
        filter_todos(job_id) {
            if(this.todos) {
                return this.todos.filter( data => data.job_id == job_id);
            } else {
                return this.dummy_todos.filter( data => data.job_id == job_id);
            }
            
        },
        updateStatus(id, status, data) {

            this.updateTask({ id: id, status: status })
                        .then(res => {
                            if(res.success) {

                                if(status != "cancelled" && status != "completed") {

                                this.mail_template(data, status)

                                this.SEND_MAIL(this.mail)
                                    .then(res => {
                                        if(res.success) {
                                            this.$notify({
                                                verticalAlign: 'top',
                                                horizontalAlign: 'right',
                                                type: 'info',
                                                message: 'Admin has been notified with the task status.'
                                            })
                                        } else {
                                            this.$notify({
                                                verticalAlign: 'top',
                                                horizontalAlign: 'right',
                                                type: 'danger',
                                                message: 'Failed to notify admin with the task status.'
                                            })
                                            console.log(res);
                                        }
                                    })
                                    
                                    
                                }
                                this.fetchJobs();
                                this.FETCH_TODOS();
                            }
                        })
        },
        submit_todo(job_id, event) {
            this.form.job_id = job_id;
            this.form.todo_title = event.target.todo_title.value;
            this.ADD_TODO(JSON.stringify(this.form))
                        .then(res => {
                            if(res.success) {
                                this.$notify({
                                    verticalAlign: 'top',
                                    horizontalAlign: 'right',
                                    type: 'success',
                                    message: res.success
                                })
                                this.FETCH_TODOS();
                            } else {
                                this.$notify({
                                    verticalAlign: 'top',
                                    horizontalAlign: 'right',
                                    type: 'danger',
                                    message: res.message
                                })
                            }
                        });
        },
        remove_todo(todo) {
            let conf = confirm('Are you want to remove "'+todo.todo_title+'"?');

            if (conf) {
            this.DELETE_TODO(JSON.stringify(todo))
                .then(res => {
                    if(res.success) {
                        this.$notify({
                            verticalAlign: 'top',
                            horizontalAlign: 'right',
                            type: 'success',
                            message: res.success
                        })
                        this.FETCH_TODOS();
                    } else {
                        this.$notify({
                            verticalAlign: 'top',
                            horizontalAlign: 'right',
                            type: 'danger',
                            message: res.message
                        })
                    }
                });
            }
        },
        update_todo(todo_id, event) {
            // Use event.target.todo_title.value to get the value in input field
            this.UPDATE_TODO(JSON.stringify({ id: todo_id, todo_title: event.target.todo_title.value}))
                .then( res => {
                    if (res.success) {
                        this.$notify({
                            verticalAlign: 'top',
                            horizontalAlign: 'right',
                            type: 'success',
                            message: res.success
                        })
                        this.FETCH_TODOS();
                    } else {
                        this.$notify({
                            verticalAlign: 'top',
                            horizontalAlign: 'right',
                            type: 'danger',
                            message: res.message
                        })
                    }
                })
        },
        update_job(job_id, column, event) {

            if (column == 'n_size') {
                this.updateTask({ id: job_id, n_size: event.target.n_size.value})
                    .then(res => {
                        this.notify(res, "N-Size Updated");
                    })
            }

            if (column == 'num_of_resp') {
                this.updateTask({ id: job_id, num_of_resp: event.target.num_of_resp.value})
                    .then(res => {
                        this.notify(res, "# of Respondents/Participants Updated");
                    })
            }

            if (column == 'num_of_client_users') {
                this.updateTask({ id: job_id, num_of_client_users: event.target.num_of_client_users.value})
                    .then(res => {
                        this.notify(res, "# of Client Users Updated");
                    })
            }

        },
        notify(res, msg) {
            if (res.success) {
                return this.$notify({
                    verticalAlign: 'top',
                    horizontalAlign: 'right',
                    type: 'success',
                    message: msg
                })
                this.FETCH_TODOS();
            } else {
                return this.$notify({
                    verticalAlign: 'top',
                    horizontalAlign: 'right',
                    type: 'danger',
                    message: res.message
                })
            }
        },
        mail_template(data, status) {
            this.FETCH_ALL_ADMINS();

            let admin_recipients = this.admin_users.map(data => { return data.email }).join(',').toString();
            
            this.mail.to = admin_recipients;
            this.mail.from = this.$session.get('email');
            this.mail.subject = "EJM_Notification - TaskID: "+ data.project.id +" - "+status.replace("_", " ").toUpperCase();
            this.mail.message = "<b>Project name:</b> "+data.project.name+"<br/>"+
                                "<b>N-size:</b> "+data.n_size+"<br/>"+
                                "<b>Number of Respondents/Participants/Nodes:</b> "+data.num_of_resp+"<br/>"+
                                "<b>Number of client users:</b> "+data.num_of_client_users+"<br/>";
            
            
        }
    }
    
}
</script>

<style scoped>
.readonly {
    border-color: transparent;
    color: white;
    background-color: transparent;
}
.columnheader {
    cursor: pointer;
}
</style>