<template>
    <div class="container-fluid">
        <img class="bg-image" src="img/bg5.jpg">

        <div class="welcome">
            <div class="welcome-body" v-if="!loadingUser">
                <h1>Welcome</h1>
                <img class="avatar" width="100"  v-bind:src="profile_image"/>
                <h1 class="welcome-title">{{ username }}</h1>
                <router-link to="/user-dashboard"><b-button class="btn-start" variant="primary">START</b-button></router-link>
            </div>

            <div class="welcome-body" v-if="loadingUser">
                <h1 class="welcome-title">Connecting...</h1>
            </div>

        </div>

    </div>
    
</template>

<script>

import { mapGetters, mapActions } from 'vuex';

export default {
    data() {
        return {
            username: '',
            loadingUser: true,
            profile_image: '',
        }
    },
    async created() {
         await this.fetchUsers()
                    .then( res => {
                       this.loadingUser = false;
                    });
         await this.checkIfUserExist();

    },
    methods: {
        ...mapActions(['fetchUsers']),
        
        checkIfUserExist() {

            // route if user is not in the record
            if(this.user == null) {

                this.$router.push('/register')

            }else{

                this.$session.start()
                this.$session.set('id', this.user.id)
                this.$session.set('user', this.user.fname)

                this.username = this.$session.get('user');

                
            }

        }
    },
    computed: {
        ...mapGetters(['user'])
    }
}
</script>

<style scoped>

.welcome{
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    background-color: rgba(250,250,250,0.3);
    padding: 1em;
    width: 60%;
    height: 50vh;
    border-radius: 5px;
    text-align: center;
}
.welcome-body{
    position: absolute;
    left: 50%;
    top: 50%;
    width: inherit;
    transform: translate(-50%,-50%);
}
.welcome-title{
    font-size: 56px;
}
.bg-image{
    width: 100%;
    border-radius: 0;
    height: 100vh;
    opacity: 0.3;

}
.container-fluid{
    margin: 0 !important;
    padding: 0 !important;

}
.avatar {
    border-radius: 50%;
}
</style>