<template>
    <div class="wrapper">
    <div class="main-panel">
    <div class="content">
        <div class="row">
            <!-- Add Project -->
            <div class="col-md-7 text-left">
                <b-card title="Add Project">
                    <b-form @submit.prevent="addProject()" class="form-inline">
                        <b-form-input
                        placeholder="Project Name"
                        v-model="projectForm.name"
                        class="col-6"
                        required
                    ></b-form-input>
                    
                        <b-form-input
                        placeholder="JIRA"
                        v-model="projectForm.osi"
                        class="col-4"
                        required
                    ></b-form-input>
                        <b-button v-if="!addLoading" type="submit" size="sm" class="btn btn-success ml-3">ADD</b-button>
                        <b-button v-if="addLoading" type="submit" size="sm" class="btn btn-success ml-3" disabled>ADDING...</b-button>
                    </b-form>
                </b-card>
            </div>
        </div>
        <div class="row">
                    
            <div class="col-md-4 col-sm-12">
                <card class="text-left">
                    <h4 class="title">Task</h4>
                    <h6 class="card-subtitle text-muted">Create your task</h6>

                    <b-form @submit.prevent="submitForm" class="mt-2" required>

                        <div class="form-group">
                            <select class="form-control" @change="setWorkDesc()" v-model="form.name" name="job_name">
                                <option value="null" disabled selected>-- Please select work type --</option>
                                <option class="text-default" v-for="(item, index) in GET_WORKTYPES" :key="index" :items="item" :value="item.title">
                                    {{ item.title }}
                                </option>
                            </select>
                        </div>

                        <div class="form-group">
                            <label>Project Name</label><br/>
                            
                            <div class="form-inline">
                                <select class="form-control col-10 mr-1" v-model="form.projectName" name="projectName">
                                    <option value="null" disabled selected>-- Please select project --</option>
                                    <option class="text-default" v-for="(item, index) in PROJECTS" :key="index" :items="item" :value="item.id">
                                        {{ item.name }}
                                    </option>
                                </select>
                                <b-button class="btn-info btn-round" size="sm" @click="showModal=true"><span class="tim-icons icon-zoom-split"></span></b-button>
                            </div>
                        </div>

                        <div class="form-group" v-show="false">
                            <b-form-input type="number" min="0" v-model="form.nodes" placeholder="Number of nodes" required/>
                        </div>

                        <div class="form-group">
                            <label>N-Size</label>
                            <base-input addon-left-icon="tim-icons icon-chart-pie-36" type="number" min="0" v-model="form.n_size" required></base-input>
                        </div>

                        <div class="form-group">
                            <label>Number of Respondents/Participants/Nodes</label>
                            <base-input addon-left-icon="tim-icons icon-vector" type="number" min="0" v-model="form.num_of_resp" required></base-input>
                        </div>

                        <div class="form-group">
                            <label>Number of Client users</label>
                            <base-input addon-left-icon="tim-icons icon-single-02" type="number" min="0" v-model="form.num_of_client_users" required></base-input>
                        </div>

                        <div class="form-group text-right">
                            <input type="submit" v-if="!loading" value="Add" class="btn btn-round btn-sm btn-primary">
                            <input type="button" v-if="loading" disabled value="Adding..." class="btn btn-round btn-sm btn-primary">
                        </div>

                    </b-form>

                </card>
            </div>

            <div class="col-md-5 col-sm-12">

                <b-alert show variant="success" align="left">
                    <i class="tim-icons icon-alert-circle-exc"></i> For any doubts, go to <strong>EES TaskForce</strong> for assessment
                    <br/>
                </b-alert>              


                <b-card
                    v-for="(item, index) in work_desc"
                    :key="index"
                    :item="item"
                    border-variant="primary"
                    header-bg-variant="info"
                    header-text-variant="white"
                    align="left"
                >

                <template v-slot:header>
                    
                    <h4><i class="tim-icons icon-bulb-63"></i> {{ item.title }}</h4>
                </template>

                    <b-card-text>
                        <p class="text-muted">GUIDANCE</p>
                        <p>
                            {{ item.description }}
                        </p>
                    </b-card-text>
                </b-card>
            </div>

        </div>

        <b-modal size="lg" v-model="showModal" id="modal-1" title="PROJECT LIST">
            <b-card>
                <base-input placeholder="Search..." v-model="search_project"></base-input>
                    <b-table
                    :filter="search_project"
                    :items="PROJECTS"
                    :fields="projects_column"
                    :per-page="searchPerPage"
                    :current-page="searchCurrentPage"
                    >
                        <template v-slot:cell(action)="data">
                            <b-button class="btn-round btn-info" @click="useProject(data.item);showModal=false" size="sm">USE</b-button>
                        </template>

                    </b-table>

                <b-pagination align="center" v-if="PROJECTS" v-model="searchCurrentPage" :per-page="searchPerPage" :total-rows="PROJECTS.length"></b-pagination>
            </b-card>

            <template v-slot:modal-footer>
                <b-button size="sm" @click="showModal=false">CANCEL</b-button>
            </template>
        </b-modal>
    </div>
    </div>
    </div>
</template>



<script>

import { mapActions, mapGetters } from 'vuex'

export default {
    name: 'CreateTask',
    data() {
        return {
            mail: {
                to: '',
                from: '',
                subject: '',
                message: '',
            },
            loading: false,
            addLoading: false,
            showModal: false,
            searchPerPage: 3,
            searchCurrentPage: 1,
            search_project: '',
            projects_column: [
                'name',
                'osi',
                'action'
            ],
            form: {
                projectName: null,
                name: null,
                nodes: 0,
                n_size: null,
                num_of_resp: null,
                num_of_client_users: null,
                owner: this.$session.get('id'),
            },
            work_desc: [],
            projectForm: {
                name: '',
                osi: '',
            },
        }
    },
    created() {
        this.fetchJobs();
        this.FETCH_WORKTYPES();
        this.GET_PROJECTS();
        this.FETCH_ALL_ADMINS();
    },
    computed: {
        ...mapGetters(['GET_WORKTYPES','PROJECTS','allJobs','admin_users']),
    },
    methods: {
        ...mapActions([
            'fetchJobs',
            'FETCH_WORKTYPES',
            'GET_PROJECTS',
            'SEND_MAIL',
            'ADD_PROJECT',
            'FETCH_ALL_ADMINS',
        ]),

        useProject(item) {
            this.form.projectName = item.id;
        },
        setWorkDesc() {
            this.work_desc = this.GET_WORKTYPES.filter( res => { return res.title == this.form.name });
        },
        error(text) {
            this.$notify({
                verticalAlign: 'top',
                horizontalAlign: 'right',
                type: 'danger',
                message: text
            })
        },
        submitForm() {
            let conf = true;

            if (this.form.name == null) {
                conf = false;
                this.error('Please select work type');
            }

            if (this.form.projectName == null) {
                conf = false;
                this.error('Please enter the project name');
            }

            if (conf == true) {

            this.loading = true;

            this.$store.dispatch('createTask', JSON.stringify(this.form))
                .then( res => {

                    if(res == true) {

                        this.mail_template();

                        this.loading = false;

                        this.$notify({
                            verticalAlign: 'top',
                            horizontalAlign: 'right',
                            type: 'success',
                            message: 'Task successfully created. Please check your dashboard'
                        })

                        this.form.name = null;
                        this.form.projectName = null;
                        this.form.n_size = null;
                        this.form.num_of_resp = null;
                        this.form.num_of_client_users = null;

                        

                        this.SEND_MAIL(this.mail)
                            .then( res => {
                                if(res.success) {
                                    this.notify('info','Request has been sent to admin for approval.')
                                } else {
                                    this.notify('danger','Request failed to sent for approval.')
                                    console.log(res);
                                }
                            });
                        
                    } else {
                        this.loading = false;
                        this.$notify({
                            verticalAlign: 'top',
                            horizontalAlign: 'right',
                            type: 'danger',
                            message: res
                        })
                    }
                   
                })
            }
        },
        notify(type, msg) {
            this.$notify({
                verticalAlign: 'top',
                horizontalAlign: 'right',
                type: type,
                message: msg
            })
        },
        mail_template() {
            this.FETCH_ALL_ADMINS();

            let project_array = this.PROJECTS.filter( data => { return data.id === this.form.projectName });
            let project_item = null;

            let admin_recipients = this.admin_users.map(data => { return data.email }).join(',').toString();

            project_array.forEach( data => {
                project_item = data;
            })

            this.mail.to = admin_recipients;
            this.mail.from = this.$session.get('email');
            this.mail.subject = "EJM_Notification - TaskID: "+ project_item.id +" - REQUEST";
            this.mail.message = "<b>Project name:</b> "+project_item.name+"<br/>"+
                                "<b>N-size:</b> "+this.form.n_size+"<br/>"+
                                "<b>Number of Respondents/Participants/Nodes:</b> "+this.form.num_of_resp+"<br/>"+
                                "<b>Number of client users:</b> "+this.form.num_of_client_users+"<br/>";
        },
        addProject() {
            this.addLoading = true;

            this.ADD_PROJECT(this.projectForm)
                .then( res => {
                    if(res.success) {
                        this.notify('success', res.success);
                        this.addLoading = false;
                        this.GET_PROJECTS();

                        // clear fields
                        this.projectForm.name = '';
                        this.projectForm.osi = '';
                    } else {
                        this.error(res.message);
                        this.addLoading = false;
                    }
                })
        },
    }
}
</script>

<style>

</style>