<template>
    <div class="col-md-6 mt-5">
      
      <h3>Completed</h3>

      <b-table hover :items="completed" :key="completed.id" :fields="fields" :per-page="perPage" :current-page="currentPage">
        <template v-slot:cell(owner_image)="data")>
          <img :src="data.value" class="photo"/>
        </template>

        <template v-slot:cell(todos)="data">
          <b-button :id="'todo-toast-'+data.item.id" variant="primary" size="sm"><span class="tim-icons icon-notes"></span></b-button>
          <b-popover :target="'todo-toast-'+data.item.id" triggers="hover" placement="right" variant="danger">
            <template v-slot:title><b>TODOS</b></template>

            <span v-for="item in data.item.todos" :item="item">{{ item }}<br/></span>
          </b-popover>
        </template>

      </b-table>
      <b-pagination v-show="completed.length" v-model="currentPage" :total-rows="completed.length" :per-page="perPage" align="center"></b-pagination>
    </div>
</template>

<script>
import Axios from 'axios';

export default {
    name: 'completed-component',
    props: {
      'dbData': Array
    },
    data(){
      return {
        perPage: 5,
        currentPage: 1,
        fields: [
        {
          key:"id",
          label:"id",
          sortable:true
        },
        {
          key:"owner_image",
          label:"owner"
        },
          "name",
          "todos"
        ],
        tableData: []
      }
    },
    computed: {
      completed() {
        return this.tableData = this.dbData.filter(function(data){ return data.status == "completed" })
      },
    }
}
</script>

