<template>
  <nav class="navbar navbar-expand-lg navbar-absolute"
       :class="{'bg-white': showMenu, 'navbar-transparent': !showMenu}">
    <div class="container-fluid">
      <div class="navbar-wrapper">
        <div class="navbar-toggle d-inline" :class="{toggled: $sidebar.showSidebar}">
          <button type="button" class="navbar-toggler"
                  @click="toggleSidebar">
            <span class="navbar-toggler-bar bar1"></span>
            <span class="navbar-toggler-bar bar2"></span>
            <span class="navbar-toggler-bar bar3"></span>
          </button>
        </div>
        
        <router-link class="navbar-brand" to="/user-dashboard">{{ this.routeName }}</router-link>
        
      </div>
      <button class="navbar-toggler" type="button"
              @click="toggleMenu"
              data-toggle="collapse"
              data-target="#navigation"
              aria-controls="navigation-index"
              aria-label="Toggle navigation">
        <span class="navbar-toggler-bar navbar-kebab"></span>
        <span class="navbar-toggler-bar navbar-kebab"></span>
        <span class="navbar-toggler-bar navbar-kebab"></span>
      </button>

      <collapse-transition>
        <div class="collapse navbar-collapse show" v-show="showMenu">
          <ul class="navbar-nav" :class="$rtl.isRTL ? 'mr-auto' : 'ml-auto'">
            
            <h3 class="text-secondary" style="font-size:18px;margin:auto">
              Hello {{ getFirstname() }}!
            </h3>
            
            <base-dropdown tag="li"
                           :menu-on-right="!$rtl.isRTL"
                           title-tag="a"
                           class="nav-item"
                           menu-classes="dropdown-navbar">
              <a slot="title" href="#" class="dropdown-toggle nav-link" data-toggle="dropdown" aria-expanded="true">
                <div class="photo">
                  <img :src="getProfileImage()">
                  
                </div>
                <b class="caret d-none d-lg-block d-xl-block"></b>
                <p class="d-lg-none">
                  Log out
                </p>
              </a>
              <li class="nav-link">
                <router-link to="/job-monitor" class="nav-item dropdown-item" target="_blank">Job Monitor</router-link>
              </li>
              <li class="nav-link">
                <router-link to="/user-profile" class="nav-item dropdown-item">Profile</router-link>
              </li>
              <li class="dropdown-divider"></li>
              <li class="nav-link separator">
                <a href="#" @click="logout()" class="nav-item dropdown-item text-danger">Logout</a>
              </li>
            </base-dropdown>
          </ul>
        </div>
      </collapse-transition>
    </div>
  </nav>
</template>
<script>
  import { CollapseTransition } from 'vue2-transitions';
  import Modal from '@/components/Modal';
import { mapGetters, mapActions } from 'vuex';

  export default {
    components: {
      CollapseTransition,
      Modal
    },
    props: {
      title: String
    },
    mounted() {
      this.fetchUsers({lanid: this.$session.get('lanid')});
    },
    computed: {
      routeName() {
        const { name } = this.$route;
        return this.capitalizeFirstLetter(name);
      },
      isRTL() {
        return this.$rtl.isRTL;
      },
      ...mapGetters(['user'])
    },
    data() {
      return {
        activeNotifications: false,
        showMenu: false,
        searchModalVisible: false,
        searchQuery: '',
        firstname: '',
      };
    },
    methods: {
      ...mapActions(['fetchUsers']),

      logout(){
        this.$session.destroy();
        this.$router.push('/');
      },
      getFirstname() {
        if(this.user) {
          this.firstname = this.user.fname;
        }
        return this.firstname;
      },
      getProfileImage() {
        if(this.user) {
          this.profileImage = this.user.image;
        }
        return "http://"+this.profileImage;
      },
      capitalizeFirstLetter(string) {
        return string.charAt(0).toUpperCase() + string.slice(1);
      },
      toggleNotificationDropDown() {
        this.activeNotifications = !this.activeNotifications;
      },
      closeDropDown() {
        this.activeNotifications = false;
      },
      toggleSidebar() {
        this.$sidebar.displaySidebar(!this.$sidebar.showSidebar);
      },
      hideSidebar() {
        this.$sidebar.displaySidebar(false);
      },
      toggleMenu() {
        this.showMenu = !this.showMenu;
      }
    }
  };
</script>
<style>
</style>
