<template>
    <div style="height: 100%;overflow:hidden">
        <card style="height: inherit">
          <template slot="header">
            <h3 class="card-title">Queue Board</h3>
            <!-- <h3 class="card-title"><i class="tim-icons icon-delivery-fast text-info "></i> 3,500</h3> -->
          </template>
          
          <div class="chart-area" style="overflow-y: scroll;overflow-x:hidden">
              <b-table v-if="data.length > 0" :items="data" :fields="columns" :per-page="perPage" :current-page="currentPage">

                <template v-slot:cell(owner_name)="data">
                    <div class="text-left">
                        <img :src="data.item.owner_image" class="photo" /> <span class="ml-2">{{ data.item.owner_name.toUpperCase() }}</span>
                    </div>
                </template>

                <template v-slot:cell(status)="data">
                    <span v-if="data.item.status == 'in_queue'" class="badge badge-warning">IN QUEUE</span>
                    <span v-if="data.item.status == 'approved'" class="badge badge-success">APPROVED</span>
                </template>

                <template v-slot:cell(processingDate)="data">
                    {{ formatDate(data.item.processingDate) }}
                </template>
                
              </b-table>
              <h3 v-if="data.length == 0">NO JOBS IN QUEUE</h3>

          </div>

        <template v-slot:footer>
            <b-pagination
                align="center"
                v-if="data.length > perPage"
                size="sm"
                v-model="currentPage"
                :per-page="perPage"
                :total-rows="data.length"
            ></b-pagination>
        </template>
          
        </card>
    </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
    props: {
        'data': Array
    },
    data() {
        return {
            columns: [
                {
                    key: 'owner_name',
                    label: 'Owner'
                },
                {
                    key: 'created_at',
                    label: 'Date Requested'
                },
                'status'
            ],
            perPage: 5,
            currentPage: 1,
        }
    },
    computed: {
        ...mapGetters(['allJobs'])
    },
    methods: {
        formatDate: (date) => {
            return new Date(date).toDateString();
        },
    }
}
</script>

<style scoped>
.card table tbody tr{
    transition: all 0.2s linear 0s;
    overflow: hidden;
}
.card table tbody tr:hover {
    box-shadow: 0px 1px 0px 0px lightgray;
    transform: scale(1.1);
    cursor: pointer;
    background-color: #e14eca;
}
</style>