import axios from 'axios';

const ip = window.location.protocol + "//" + window.location.hostname;
const port = process.env.VUE_APP_PORT;

const state = {
    worktypes: [],
};

const mutations = {
    SET_WORKTYPES: (state, data) => state.worktypes = data,
};

const actions = {
    async FETCH_WORKTYPES({ commit }) {
        const response = await axios.get(`${ip}:${port}/ejm/api/worktypes/show.php`);
        commit('SET_WORKTYPES', response.data.records);
    },
    async CREATE_WORKTYPE({ commit }, data) {
        const response = await axios.post(`${ip}:${port}/ejm/api/worktypes/create.php`, data);
        return response.data;
    },
    async REMOVE_WORKTYPE({ commit }, data) {
        const response = await axios.post(`${ip}:${port}/ejm/api/worktypes/remove.php`, data);
        return response.data;
    },
    async UPDATE_WORKTYPE({ commit }, data) {
        const response = await axios.patch(`${ip}:${port}/ejm/api/worktypes/update.php`, data);
        return response.data;
    },
};

const getters = {
    GET_WORKTYPES: state => state.worktypes
};

export default {
    state,
    mutations,
    actions,
    getters
}