import axios from 'axios';

const ip = window.location.protocol + "//" + window.location.hostname;
const port = process.env.VUE_APP_PORT;

const state = {
	todos: []
};

const mutations = {
	SET_TODOS: ( state, todos) => state.todos = todos,
};

const actions = {
	async FETCH_TODOS({ commit }) {
		const response = await axios.get(`${ip}:${port}/ejm/api/todos/show.php`);
		commit('SET_TODOS', response.data.records);
	},
	async ADD_TODO({ commit }, data) {
		const response = await axios.post(`${ip}:${port}/ejm/api/todos/create.php`, data);
		return response.data;
	},
	async DELETE_TODO({ commit }, data) {
		const response = await axios.post(`${ip}:${port}/ejm/api/todos/delete.php`, data);
		return response.data;
	},
	async UPDATE_TODO({ commit }, data) {
		const response = await axios.post(`${ip}:${port}/ejm/api/todos/update.php`, data);
		return response.data;
	}
};

const getters = {
	GET_TODOS: state => state.todos,
};

export default {
    state,
    getters,
    actions,
    mutations
}