<template>

  <div class="wrapper">
    <div class="main-panel">

      <div class="content">
        <div class="row">

            <!-- File Upload -->
            <div class="col-md-5 text-left">
                <b-card title="Bulk Upload">
                    <b-form @submit.prevent="bulkCreate()">
                        <b-form-file
                            size="sm"
                            placeholder="Please select file for bulk upload (.csv)"
                            accept=".csv"
                            v-model="csvFile"
                            required
                        ></b-form-file>

                        

                        <b-button v-if="csvFile" type="submit" size="sm" class="btn btn-primary">UPLOAD</b-button>
                        <b-button v-if="!csvFile" size="sm" class="btn btn-primary" disabled>UPLOAD</b-button>
                    </b-form>

                    <template v-slot:footer>
                        <label class="text-danger">* CSV file column name in order: <span class="text-success">[ project, osi ]</span></label>
                        <div v-if="notUploadedProjects">
                            Duplicate records: <a :href="'data:text/plain;charset=utf-8,'+notUploadedProjects" download="Not Uploaded Records">Download File</a>
                        </div>
                    </template>
                </b-card>
            </div>

            <!-- Add Project -->
            <div class="col-md-7 text-left">
                <b-card title="Add Project">
                    <b-form @submit.prevent="addProject()" class="form-inline">
                        <b-form-input
                        placeholder="Project Name"
                        v-model="projectForm.name"
                        class="col-6"
                        required
                    ></b-form-input>
                    
                        <b-form-input
                        placeholder="JIRA"
                        v-model="projectForm.osi"
                        class="col-4"
                        required
                    ></b-form-input>
                        <b-button v-if="!addLoading" type="submit" size="sm" class="btn btn-success ml-3">ADD</b-button>
                        <b-button v-if="addLoading" type="submit" size="sm" class="btn btn-success ml-3" disabled>ADDING...</b-button>
                    </b-form>
                </b-card>
            </div>

            <!-- Projects Table -->
            <div class="col-md-8">
                <b-card>
                    <template v-slot:header>
                        <h4 class="title">Projects</h4>
                        <h6 class="card-subtitle text-muted">List of Projects</h6>
                    </template>

                    <div>
                        <b-form-input type="text" v-model="search" placeholder="Search..."></b-form-input>
                    </div>

                    <b-table
                        :filter="search"
                        :items="PROJECTS"
                        :fields="fields"
                        :per-page="perPage"
                        :current-page="currentPage"
                        :busy="isBusy"
                        selectable
                        select-mode="single"
                        @row-selected="OnRowSelected"
                        class="text-left"
                    >

                    <template v-slot:table-busy>
                        <div class="text-center text-danger my-2">
                        <b-spinner class="align-middle"></b-spinner>
                        <strong>Loading...</strong>
                        </div>
                    </template>

                    </b-table>

                    <template v-slot:footer>
                        <b-pagination v-if="PROJECTS" align="center" :per-page="perPage" v-model="currentPage" :total-rows="PROJECTS.length"></b-pagination>
                    </template>

                </b-card>
            </div>

            <!-- Update form -->
            <div class="col-md-4">
                <b-form v-if="project">
                    <b-card v-if="project" class="text-left">

                        <template v-slot:header>
                            <h4 class="title">Update Project</h4>
                        </template>

                        <div class="form-group">
                            <label>PROJECT NAME</label>
                            <b-form-input v-model="form.name" :value="project.name" name="name" required></b-form-input>
                        </div>
                        
                        <div class="form-group">
                            <label>OSI Ticket</label>
                            <b-form-input v-model="form.osi" :value="project.osi" name="osi" required></b-form-input>
                        </div>

                        <template v-slot:footer class="text-right">
                            <b-button @click="removeItem()" size="sm" class="btn btn-danger">REMOVE</b-button>

                            <b-button v-if="!updateLoading" @click="updateItem()" size="sm" class="btn btn-info" style="float:right">UPDATE</b-button>
                            <b-button v-if="updateLoading" size="sm" class="btn btn-info" style="float:right" disabled>UPDATING...</b-button>
                        </template>

                    </b-card>
                </b-form>
            </div>

        </div>
      </div>

    </div>
  </div>

</template>

<script>
import { mapActions, mapGetters } from 'vuex'
export default {
    data() {
        return {
            search: '',
            perPage: 5,
            currentPage: 1,
            totalRows: 0,
            project: null,
            isBusy: true,
            updateLoading: false,
            addLoading: false,
            notUploadedProjects: '',
            csvFile: null,
            form: {
                id: null,
                name: '',
                osi: ''
            },
            projectForm: {
                name: '',
                osi: '',
            },
            fields: [
                {
                    key: 'id',
                    label: 'ID'
                },
                {
                    key: 'name',
                    label: 'Name'
                },
                {
                    key: 'osi',
                    label: 'JIRA'
                },
                {
                    key: 'created_at',
                    label: 'Created At'
                },
                {
                    key: 'updated_at',
                    label: 'Updated At'
                },
            ],
        }
    },
    mounted() {
        this.GET_PROJECTS()
            .then( res => {
                this.isBusy = false;
                this.totalRows = this.PROJECTS.length;
            });
        this.fetchUsers({lanid: this.$session.get('lanid')})
            .then(res => {
                this.check_admin();
            })
    },
    methods: {
        ...mapActions([
            'GET_PROJECTS',
            'UPDATE_PROJECT',
            'ADD_PROJECT',
            'REMOVE_PROJECT',
            'BULK_CREATE',
            'fetchUsers'
        ]),
        check_admin() {
        if (this.user) {
            this.user_roles = this.user.roles.split(',');

            // If not admin redirect to user dashboard
            if(this.user_roles.indexOf('role_Admin') == -1) {
                this.$router.push('/user-dashboard')
            }
        }

        },

        OnRowSelected(item) {
            item.forEach( (data, index) => {
                this.project = data;

                // Assign Values
                this.form.id = this.project.id;
                this.form.name = this.project.name;
                this.form.osi = this.project.osi;
            });
        },
        addProject() {
            this.addLoading = true;

            this.ADD_PROJECT(this.projectForm)
                .then( res => {
                    if(res.success) {
                        this.notification('success', res.success);
                        this.addLoading = false;
                        this.GET_PROJECTS();

                        // clear fields
                        this.projectForm.name = '';
                        this.projectForm.osi = '';
                    } else {
                        this.notification('danger', res.message);
                        this.addLoading = false;
                    }
                })
        },
        bulkCreate() {
            let data = new FormData();
            data.append('csv',this.csvFile);

            this.BULK_CREATE(data)
                .then( res => {
                    if(res.success) {
                        this.notification('success', res.success);
                        this.csvFile = null;
                        this.GET_PROJECTS();
                    } else {
                        this.notification('danger', res.message);
                    }

                    if(res.additional_data) {
                        res.additional_data.forEach( data => {
                            this.notUploadedProjects += data.name+"\t"+data.osi+"\n";
                        })
                    }
                })
        },
        removeItem() {
            let conf = window.confirm("Are you sure you want to delete this item?\n\n" + this.form.name);

            if(conf) {
                this.REMOVE_PROJECT(this.form)
                    .then( res => {
                        if(res.success) {
                            this.notification('success', res.success);
                            this.GET_PROJECTS();
                            this.project = null;
                        } else {
                            this.notification('danger', res.message);
                        }
                    })
            }
            
        },
        updateItem() {
            this.updateLoading = true;

            this.UPDATE_PROJECT(this.form)
                .then( res => {
                    if(res.success) {
                        this.notification('success', res.success);
                        this.updateLoading = false;
                        this.GET_PROJECTS();
                    } else {
                        this.notification('danger', res.message);
                        this.updateLoading = false;
                    }
                });
        },
        notification(type, msg) {
            this.$notify({
                verticalAlign: 'top',
                horizontalAlign: 'right',
                type: type,
                message: msg
            })
        }
    },
    computed: {
        ...mapGetters(['PROJECTS','user'])
    }

}
</script>

