import axios from 'axios';

const ip = window.location.protocol + "//" + window.location.hostname;
const port = process.env.VUE_APP_PORT;

const state = {
    jobs: [],
}

const mutations = {
    SET_JOBS: (state, jobs) => state.jobs = jobs,
}

const actions = {

    async fetchJobs({ commit }) {
        const response = await axios.get(`${ip}:${port}/ejm/api/Jobs/show.php`);
        
        !response.data.message ? commit('SET_JOBS', response.data.records) : response.data.message;
        
    },
    async createTask({ commit }, task) {

        const response = await axios.post(`${ip}:${port}/ejm/api/Jobs/create.php`, task);

        if(response.status === 200 && !response.data.message) {
            commit('SET_JOBS', response.data.records)
            return true;
        } else {
            return response.data.message;
        }
        
    },
    async updateTask({ commit, actions }, data) {
        const response = await axios.post(`${ip}:${port}/ejm/api/Jobs/update.php`, JSON.stringify(data));
        return response.data;
    }
}

const getters = {
    allJobs: state => state.jobs,
}

export default {
    state,
    getters,
    actions,
    mutations
}