<template>
    <div class="wrapper">
        <div class="main-panel">

            <div class="content">
                <div class="row">
                    <div class="col-md-6">
                        <b-card
                            align="left"
                            header-bg-variant="default"
                        >
                        <template v-slot:header>
                            
                            <h4><b class="tim-icons icon-simple-add"></b> CREATE WORK TYPE</h4>
                        </template>

                            <b-form @submit.prevent="add_worktype()">

                                <div class="form-group">
                                    <b-form-input v-model="form.title" type="text" placeholder="Enter your title" required/>
                                </div>
                                
                                <div class="form-group">
                                    <b-form-input v-model="form.description" type="text" placeholder="Enter your description" />
                                </div>

                                <div class="form-group">
                                    <b-button type="submit" v-if="!loading" size="sm" variant="success">CREATE</b-button>
                                    <b-button type="button" v-if="loading" disabled size="sm" variant="success">Creating...</b-button>
                                </div>
                            </b-form>

                        </b-card>
                    </div>

                    <div class="col-md-8">
                        <b-card
                            title="Work Types"
                            sub-title="List of work types"
                            align="left"
                        >

                            <b-table
                                selectable
                                select-mode="single"
                                :items="GET_WORKTYPES"
                                :fields="fields"
                                @row-selected="onRowSelected"
                                :per-page="perPage"
                                :current-page="currentPage"
                            >

                            </b-table>

                            <b-pagination align="center" v-model="currentPage" :per-page="perPage" :total-rows="GET_WORKTYPES.length"></b-pagination>

                        </b-card>
                    </div>

                    <div class="col-md-4">
                        <WorkTypeCard v-if="worktype" :worktype="worktype" />
                    </div>

                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import WorkTypeCard from '@/layout/starter/Cards/WorkTypeCard.vue'

export default {
    data() {
        return {
            loading: false,
            perPage: 5,
            currentPage: 1,
            showCard: false,
            fields: [
                { key: 'title' },
                { key: 'description' },
            ],
            worktype: null,
            form: {
                title: '',
                description: ''
            }
        }
    },
    components: {
        WorkTypeCard,
    },
    created() {
        this.FETCH_WORKTYPES();
        this.fetchUsers({lanid:this.$session.get('lanid')})
            .then(res => {
                this.check_admin();
            })
    },
    computed: {
        ...mapGetters(['GET_WORKTYPES','user']),
    },
    methods: {
        ...mapActions(['FETCH_WORKTYPES','CREATE_WORKTYPE','fetchUsers']),

        check_admin() {
            if (this.user) {
                this.user_roles = this.user.roles.split(',');

                // If not admin redirect to user dashboard
                if(this.user_roles.indexOf('role_Admin') == -1) {
                    this.$router.push('/user-dashboard')
                }
            }
        },
        onRowSelected(items) {
            items.forEach( (data, index) => {
                this.worktype = data;
            });
        },
        add_worktype() {
            this.loading = true;

            this.CREATE_WORKTYPE(JSON.stringify(this.form))
                .then( res => {
                    if(res.success) {
                        this.notify('success', res.success);
                        this.FETCH_WORKTYPES();
                        this.loading = false;
                
                        // clear field
                        this.form.title = '';
                        this.form.description = '';
                    } else {
                        this.notify('danger', res);
                        this.loading = false;
                    }
                });
        },
        notify(type, message) {
            this.$notify({
                verticalAlign: 'top',
                horizontalAlign: 'right',
                type: type,
                message: message
            });
        }
    }
}
</script>

