<template>
  <card type="user">
    <p class="card-text">
    </p>
    <div class="author">
      <div class="block block-one"></div>
      <div class="block block-two"></div>
      <div class="block block-three"></div>
      <div class="block block-four"></div>

        <img class="avatar" :src="user.image" alt="...">
        <!-- <h5 class="title" style="text-transform: uppercase">{{user.fname+" "+user.lname}}</h5> -->
      <p class="description">

          <b-form @submit.prevent="updateUser()" inline>
              <!-- Firstname -->
              <div class="col-md-6 text-left">
                <h4>Firstname</h4>
                <b-form-input type="text" name="fname" v-model="user.fname" required/>
              </div>
  
              <!-- Lastname -->
              <div class="col-md-6 text-left">
                <h4>Lastname</h4>
                <b-form-input type="text" name="lname" v-model="user.lname" required/>
              </div>
              <!-- LANID -->
              <div class="col-md-6 text-left mt-2">
                <h4>LANID</h4>
                <b-form-input type="text" name="lanid" v-model="user.lanid" required/>
              </div>
  
              <!-- LANID -->
              <div class="col-md-6 text-left">
                <h4>PC Name</h4>
                <b-form-input type="text" name="pc_name" v-model="user.pc_name" required/>
              </div>

              <!-- Email -->
              <div class="col-md-6 text-left">
                <h4>Email</h4>
                <b-form-input type="text" name="email" v-model="user.email" required/>
              </div>

              <!-- Region -->
              <div class="col-md-6 text-left">
                <h4>Region</h4>
                <select class="form-control" v-model="user.region" name="region" required>
                    <option value="null" disabled selected>-- Please select region --</option>
                    <option class="text-default" v-for="(item, index) in regions" :key="index" :items="item" :value="item.value">
                        {{ item.text }}
                    </option>
                </select>
              </div>

            <div class="col-md-12 text-left mt-3">
              <h4>
                List of Roles<br/>
                <small class="text-muted">Please select roles for the user</small>
              </h4>
              
              
              <div class="form-group">
                {{ update_roles }}
                <div class="custom-control custom-checkbox mr-2" v-for="(item, index) in options" :key="index" :item="item">
                  
                  <input v-model="roles" :id="item.value" type="checkbox" name="roles" autocomplete="off" class="custom-control-input" :value="item.value">
                  <label class="custom-control-label" :for="item.value">{{ item.text }}</label>
                </div>

              </div>
            </div>

            <div class="col-md-12 mt-3 roles-container">
            </div>

            <div class="col-md-12 text-right">
              <b-button size="sm" v-if="!loading" variant="info" type="submit"><b-spinner label="Spinning"></b-spinner> Update</b-button>
              <b-button size="sm" v-if="loading" variant="info" disabled>Updating...</b-button>
            </div>

          </b-form>

      </p>
    </div>
    <p></p>

    <!-- <p class="card-description">
      {{user.description}}
    </p>

    <div slot="footer" class="button-container">
      <base-button icon round class="btn-facebook">
        <i class="fab fa-facebook"></i>
      </base-button>
      <base-button icon round class="btn-twitter">
        <i class="fab fa-twitter"></i>
      </base-button>
      <base-button icon round class="btn-google">
        <i class="fab fa-google-plus"></i>
      </base-button>
    </div> -->

  </card>
</template>
<script>
import { mapActions } from 'vuex';
  export default {
    data () {
      return {
        roles: null,
        roles_str: null,
        loading: false,
        options: [
          {value: 'role_Admin', text: 'Administrator'},
          {value: 'role_TF', text: 'Task Force'},
          {value: 'role_Ma', text: 'Manager'},
          {value: 'role_CA', text: 'Configuration Analyst'},
          {value: 'role_PA', text: 'Production Analyst'},
          {value: 'role_DA', text: 'Delivery Analyst'},
          {value: 'role_TC', text: 'Team Coordinator'},
         
        ],
        regions: [
            {value: 'APAC', text: 'APAC'},
            {value: 'EMEA', text: 'EMEA'},
            {value: 'north_america', text: 'North America'},
        ]
      }
    },
    props: {
      user: {
        type: Object,
        default: () => {
          return {};
        }
      }
    },
    computed: {
      update_roles: function() {
        this.roles = this.user.roles.split(',');
      }
    },
    methods: {
      ...mapActions(['FETCH_ALL_USERS','UPDATE_USER']),

      updateUser() {

        if ( this.roles != null ) {

            if(this.roles.indexOf('role_User') == -1 ) {
              this.roles.push('role_User')
            }

            this.roles_str = this.roles.join();
            
        } else {

          if (this.roles == "" || this.roles_str == null) {
            this.roles_str = this.user.roles;
          }

        }


        let form = {
            'id': this.user.id,
            'fname': this.user.fname,
            'lname': this.user.lname,
            'roles': this.roles_str,
            'lanid': this.user.lanid,
            'email': this.user.email,
            'region': this.user.region,
            'pc_name': this.user.pc_name,
        }

        this.loading = true;

        this.UPDATE_USER(JSON.stringify(form))
            .then( res => {
              if(res.success) {

                this.loading = false;
                this.notify('success', res.success);
                this.FETCH_ALL_USERS();

                if(form.id == this.$session.get('id')) {
                  setTimeout(()=> {
                    this.$router.push('/');
                  }, 1500);
                }
                
              } else {
                this.loading = false;
                this.notify('danger', res.message);
              }

              

            })

      },
      notify(type, message) {
        this.$notify({
            verticalAlign: 'top',
            horizontalAlign: 'right',
            type: type,
            message: message
        })
      }
    }
  }
</script>
<style scoped>

</style>
