<template>
    <div class="wrapper">
    <div class="main-panel">
    <div class="content">
        <div class="row">
            <div class="col-md-6">
                <div>
                    <b-card title="Profile" sub-title="Update your profile" class="text-left" v-if="user">
                        <b-card-text>
                            <b-form @submit.prevent="updateProfile()">

                                <!-- Firstname -->
                                <div class="form-group">
                                    <base-input 
                                        type="text"
                                        name="fname"
                                        placeholder="Firstname"
                                        v-model="form.fname"
                                    >
                                    </base-input>
                                </div>

                                <!-- Lastname -->
                                <div class="form-group">
                                    <base-input 
                                        type="text"
                                        name="lname"
                                        placeholder="Lastname"
                                        v-model="form.lname"
                                    >
                                    </base-input>
                                </div>

                                <!-- Email -->
                                <div class="form-group">
                                    <base-input 
                                        type="email"
                                        name="email"
                                        placeholder="Email"
                                        v-model="form.email"
                                    >
                                    </base-input>
                                </div>

                                <!-- Region -->
                                <div class="form-group">
                                    <select class="form-control" v-model="form.region" name="region">
                                        <option value="null" disabled selected>-- Please select region --</option>
                                        <option class="text-default" v-for="(item, index) in regions" :key="index" :items="item" :value="item.value">
                                            {{ item.text }}
                                        </option>
                                    </select>
                                </div>

                                <!-- LANID -->
                                <div class="form-group">
                                    <label for="" class="text-danger">* Please note that your LANID is you login</label>
                                    <base-input 
                                        type="text"
                                        name="lanid"
                                        placeholder="LANID"
                                        v-model="form.lanid"
                                    >
                                    </base-input>
                                </div>

                                <!-- Profile Image -->
                                <div class="form-group">
                                    <div class="col">
                                        <b-form-file
                                            size="sm"
                                            placeholder="Choose profile image or drop it here"
                                            accept=".jpg, .png"
                                            v-model="form.image"
                                            @change="setImage($event)"
                                        ></b-form-file>
                                    </div>
                                </div>
                                <input v-if="!loadingUpdate" type="submit" class="btn btn-sm btn-info" value="UPDATE">
                                <input v-if="loadingUpdate" type="button" disabled class="btn btn-sm btn-info" value="Updating...">

                            </b-form>
                        </b-card-text>
                    </b-card>
                </div>

            </div>

            <div class="col-md-5" v-if="user">
                <b-card>
                    <b-card-text style="display:flex;justify-content:start;align-items:center">
                        <div class="mr-5">
                            <img :src="'http://'+user.image" alt="profile image" class="avatar">
                        </div>
                        <div style="line-height:15px;text-align:left">
                            <p style="font-size:24px;">{{ user.fname }} {{ user.lname }}</p>
                            <p class="text-muted" style="font-size:14px;text-transform:uppercase">{{ user.lanid }}</p>
                            <br/>
                            <p class="badge badge-primary text-left">
                                <span>{{ user.roles }}<br/></span>
                                
                            </p>
                        </div>
                    </b-card-text>
                </b-card>
            </div>
        </div>
    </div>
    </div>
    </div>
  
</template>

<script>
import UserCard from '@/pages/Profile/UserCard.vue'
import { mapGetters, mapActions } from 'vuex'

export default {
    components: {
        UserCard
    },
    data() {
        return {
            loadingUpdate: false,
            form: {
                id: '',
                fname: '',
                lname: '',
                lanid: '',
                email: '',
                region: '',
                image: null,
            },
            regions: [
                {value: 'APAC', text: 'APAC'},
                {value: 'EMEA', text: 'EMEA'},
                {value: 'north_america', text: 'North America'},
            ]
        }
    },
    async created() {
        await this.fetchUsers({lanid: this.$session.get('lanid')});
        await this.setFormData();
    },
    methods: {
        ...mapActions(['fetchUsers','UPDATE_PROFILE']),
        
        // explodeRoles(roles) {
        //     let role_array = roles.split(',');
        //     return role_array;
        // },
        setFormData() {
            this.form.id = this.user.id;
            this.form.fname = this.user.fname;
            this.form.lname = this.user.lname;
            this.form.lanid = this.user.lanid;
            this.form.email = this.user.email;
            this.form.region = this.user.region;
        },
        updateProfile() {
            let data = new FormData();

            data.append("id", this.form.id);
            data.append("fname", this.form.fname);
            data.append("lname", this.form.lname);
            data.append("lanid", this.form.lanid);
            data.append("email", this.form.email);
            data.append("region", this.form.region);
            
            if(this.form.image != null) {
                data.append("image", this.form.image);
            }

            this.loadingUpdate = true;

            this.UPDATE_PROFILE(data)
                .then(res => {
                    if(res.success) {

                        this.notify("success", res.success);
                        this.fetchUsers({lanid: this.form.lanid});
                        this.form.image = null;
                        this.loadingUpdate = false;

                    } else {
                        this.notify("danger", "Unable to update profile. Please try again later.");
                        this.loadingUpdate = false;
                    }
                });

            this.$session.set('user', this.form.fname);
            this.$session.set('lanid', this.form.lanid);
            this.$session.set('email', this.form.email);
            this.$session.set('region', this.form.region);

        },
        setImage(event) {
            this.form.image = event.target.files[0];
        },
        notify(type, msg) {
            this.$notify({
                verticalAlign: 'top',
                horizontalAlign: 'right',
                type: type,
                message: msg
            })
        }
    },
    computed: {
        ...mapGetters(['user'])
    }
}
</script>

<style scoped>
.avatar {
    width: 150px !important;
    height: auto !important;
    border: solid 2px white;
}
</style>