import Vue from 'vue';
import Router from 'vue-router';
import ContentLayout from '@/layout/starter/ContentLayout.vue';
import JobMonitor from '@/layout/starter/Pages/JobMonitor.vue';
import UserDashboard from '@/layout/starter/Pages/UserDashboard.vue';
import DashboardLayout from '@/layout/starter/DashboardLayout.vue';
import Welcome from '@/layout/starter/Pages/Welcome.vue';
import Register from '@/layout/starter/Pages/Register.vue'
import CreateTask from '@/layout/starter/Pages/CreateTask.vue'
import AdminDashboard from '@/layout/starter/Pages/AdminDashboard.vue'
import ManageUsers from '@/layout/starter/Pages/ManageUsers.vue'
import ManageWorkTypes from '@/layout/starter/Pages/ManageWorkTypes.vue'
import UserProfile from '@/layout/starter/Pages/UserProfile.vue'
import ManageProjects from '@/layout/starter/Pages/ManageProjects.vue'
import Login from '@/layout/starter/Pages/Login.vue'

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'login',
      redirect: '/login',
      component: ContentLayout,
      children: [
        {
          path: '/login',
          components: { default: Login},
        },
        {
          path: '/job-monitor',
          name: 'Job Monitor',
          components: { default: JobMonitor }
        },
        {
          path: '/register',
          name: 'Register',
          components: { default: Register }
        },
      ]
    },
    {
      path: '/',
      component: DashboardLayout,
      children: [
        {
          path: '/user-dashboard',
          name: 'User Dashboard',
          components: { default: UserDashboard }
        },
        {
          path: '/create-task',
          name: 'Create Task',
          components: { default: CreateTask}
        },
        {
          path: '/admin-dashboard',
          name: 'Administrator Dashboard',
          components: { default: AdminDashboard}
        },
        {
          path: '/manage-users',
          name: 'Admin | Manage Users',
          components: { default: ManageUsers },
        },
        {
          path: '/manage-worktypes',
          name: 'Admin | Work Types',
          components: { default: ManageWorkTypes }
        },
        {
          path: '/manage-projects',
          name: 'Admin | Manage Projects',
          components: { default: ManageProjects }
        },
        {
          path: '/user-profile',
          name: 'User | Profile',
          components: { default: UserProfile }
        },
      ]
    }
  ]
});
