import axios from 'axios';

const ip = window.location.protocol + "//" + window.location.hostname;
const port = process.env.VUE_APP_PORT;

const state = {
    projects: [],
}

const mutations = {
    SET_PROJECTS: (state, projects) => state.projects = projects,
}

const actions = {
    async GET_PROJECTS({ commit }) {
        const response = await axios.get(`${ip}:${port}/ejm/api/projects/show.php`);
        commit('SET_PROJECTS', response.data.records);
    },
    async ADD_PROJECT({ commit }, data) {
        const response = await axios.post(`${ip}:${port}/ejm/api/projects/create.php`, JSON.stringify(data));
        return response.data;
    },
    async BULK_CREATE({ commit }, data) {
        const response = await axios.post(`${ip}:${port}/ejm/api/projects/bulk_create.php`, data);
        console.log(response.data);
        return response.data;
    },
    async UPDATE_PROJECT({ commit }, data) {
        const response = await axios.post(`${ip}:${port}/ejm/api/projects/update.php`, JSON.stringify(data));
        return response.data;
    },
    async REMOVE_PROJECT({ commit }, data) {
        const response = await axios.post(`${ip}:${port}/ejm/api/projects/delete.php`, JSON.stringify(data));
        return response.data;
    },
}

const getters = {
    PROJECTS: state => state.projects,
}

export default {
    state,
    mutations,
    actions,
    getters
}