import Axios from "axios";

const ip = window.location.protocol+"//"+window.location.hostname;
const port = process.env.VUE_APP_PORT;

const state = {
    user: null,
    users: null,
    admin_users: null,
}

const mutations = {
    SET_USER: (state, user) => state.user = user,
    SET_USERS: (state, users) => state.users = users,
    SET_ADMINS: (state, admin_users) => state.admin_users = admin_users
}

const actions ={
    async fetchUsers({ commit }, data) {
        const response = await Axios.post(`${ip}:${port}/ejm/api/user/get_user.php`, JSON.stringify(data));
        !response.data.message ? commit('SET_USER', response.data) : '';
        return response;
    },
    async LOGIN({ commit }, data) {
        const response = await Axios.post(`${ip}:${port}/ejm/api/user/login.php`, JSON.stringify(data));
        return response.data;
    },
    async FETCH_ALL_USERS({ commit }) {
        const response = await Axios.get(`${ip}:${port}/ejm/api/user/show.php`);
        commit('SET_USERS', response.data.records);
    },
    async UPDATE_USER({ commit }, data) {
        const response = await Axios.patch(`${ip}:${port}/ejm/api/user/update.php`, data);
        return response.data;
    },
    async UPDATE_PROFILE({ commit }, data) {
        const response = await Axios.post(`${ip}:${port}/ejm/api/user/updateProfile.php`, data);
        return response.data;
    },
    async CREATE_USER({ commit }, data) {
        const response = await Axios.post(`${ip}:${port}/ejm/api/user/create.php`, data);
        return response;
    },
    async FETCH_ALL_ADMINS({ commit }) {
        const response = await Axios.get(`${ip}:${port}/ejm/api/user/get_admins.php`);
        commit('SET_ADMINS', response.data.records);
    },

}

const getters = {
    user: state => state.user,
    users: state => state.users,
    admin_users: state => state.admin_users,
};

export default {
    state,
    mutations,
    actions,
    getters,
}