<template>
    <div class="col-4" style="float:left">
        <card>
          <template slot="header">
            <!-- <h5 class="card-category">{{ data.dataLabel }}</h5> -->
            <h4 class="card-title">{{ data.dataLabel }}</h4>
          </template>
          <div class="chart-area">
              <h1 style="font-size: 100px;font-family: segoe ui">{{ set_count() }}</h1>
              <!-- <p>Lorem, ipsum dolor sit amet consectetur adipisicing elit.</p> -->
          </div>
        </card>
    </div>
</template>

<script>

export default {
    props: {allJobs: Array, label: String, status: String},
    data() {
        return {
            data: { 
                count: this.currentCount,
                dataLabel: this.label
            }
        }
    },
    methods: {
        set_count: function() {
            return this.allJobs.filter( data => {
                return data.status == this.status
            }).length
        }
    }
}
</script>

<style scoped>
.card{
    transition: all 0.2s linear 0s;
    cursor: pointer;
}
.card:hover{
    box-shadow: 0px 0px 1px 1px #e14eca;
    transform: translateY(-5%);
}
</style>