<template>

  <div class="row">
    
    <!-- In Process Table Component -->
    <in-process-table :dbData="allJobs" :todoData="GET_TODOS"></in-process-table>
    
    <div class="col-md-12 threshold-box mb-5">
        <div class="col-md-5">
          <QueueBoard :data="in_queue(allJobs)"/>
        </div>
        
        <div class="col-md-7">
          
          <Threshold :allJobs="allJobs" status="completed" label="Completed" />
          <Threshold :allJobs="allJobs" label="Total Datafile Rows" />
          <Threshold :allJobs="allJobs" label="HRIS Uploads" />
          <Threshold :allJobs="allJobs" label="RS Processing" />
          <Threshold :allJobs="allJobs" label="N-Size" />
          <Threshold :allJobs="allJobs" label="Nodes" />
        </div>
    </div>
    
  </div>


</template>
<script>

import BaseTable from '@/components/BaseTable.vue'
import Axios from 'axios';
import InQueueTable from '../Tables/InQueueTable'
import InProcessTable from '../Tables/InProcessTable'
import CompletedTable from '../Tables/CompletedTable'
import Threshold from '../Tables/Threshold'
import QueueBoard from '../Tables/QueueBoard'

// Map Getters from Store
import { mapGetters, mapActions } from 'vuex'

export default {
  name: 'starter-page',
  components: {
    BaseTable,
    InQueueTable,
    InProcessTable,
    CompletedTable,
    Threshold,
    QueueBoard,
  },
  data() {
    return {
      currentVal: [0, 1, 2, 3, 4, 5, 6],
      tableData: [],
    }
  },
  mounted() {
    this.fetchJobs();
    this.FETCH_TODOS();
  },
  created: function() {
      setInterval( () => {
        this.fetchJobs();
        this.FETCH_TODOS();
      }, 3000);
  },
  methods: {
    ...mapActions(['fetchJobs', 'FETCH_TODOS']),

    in_queue(jobs) {
      return jobs.filter(data => { 
        return data.status == 'in_queue' || data.status == 'approved'
      });
    },
  },
  computed: {
    ...mapGetters(['allJobs', 'GET_TODOS']),
  },
};
</script>
<style>
div.row{
  text-align: center !important;
}

div.threshold-box{
  display: flex;
  justify-content: space-evenly;
}
card.threshold-items{
  width: 50% !important;
}
.loading{
    position: absolute;
    left: 50%;
    top: 50%;
    z-index: 1000;
    transform: translate(-50%, -50%);
}
</style>
