<template>
    <div class="col-md-12 mt-1">
      <div class="col-md-12">
        <h3>Processing Jobs (Top 5)</h3>
      </div>

        <div class="col-md-12 mt-2 flex-container">
          
          <!-- Notify if there are no processing -->
          <card v-show="in_process.length == 0">
            <template slot="header">
              <h1 class="card-title">Hangfire is clear!</h1>
            </template>
          </card>

          <b-table
            v-if="dbData"
            :items="in_process" 
            :fields="in_process_fields" 
            id="in-process-table" 
            tbody-tr-class="table-cards"
            thead-class="table-thead"
            per-page="5"
          >

            <template v-slot:cell(card)="data">
              <b-card
                :title="data.item.name"
                style="width: 19.5rem;"
                class="mb-2"
              >

                <template v-slot:header>
                    <b-img class="avatar inprocess-avatar" :src="data.item.owner_image"/>
                    <div class="user-details">
                        <p>
                          <strong>{{ data.item.owner_name }}</strong>
                        </p>
                        <span class="text-muted" style="word-break:break-word">{{ data.item.owner_roles }}</span>
                    </div>
                </template>

                <b-card-text>

                  <h4 style="display: flex;justify-content: space-evenly;">
                    <span class="mr-2 btn btn-sm btn-primary" title="Overall N-Size"><i class="tim-icons icon-chart-pie-36"></i>&nbsp;&nbsp;{{ data.item.n_size }}</span>
                    <span class="mr-2 btn btn-sm btn-primary" title="Number of Respondents/Participants"><i class="tim-icons icon-vector"></i>&nbsp;&nbsp;{{ data.item.num_of_resp }}</span>
                    <span class="mr-2 btn btn-sm btn-primary" title="Number of Client Users"><i class="tim-icons icon-single-02"></i>&nbsp;&nbsp;{{ data.item.num_of_client_users }}</span>
                  </h4>

                  <b-table v-if="todoData" :items="filter_todos(data.item.id)" :fields="todos_fields" per-page="3" current-page="1">
                    <template v-slot:custom-foot>
                          <span class="text-muted">{{ filter_todos(data.item.id).length }} total todos</span>
                        </template>
                  </b-table>

                </b-card-text>
              </b-card>
            </template>

          </b-table>

        </div>
        <h3 v-if="dbData">{{ in_process.length }} total item/s</h3>
        
    </div>
</template>

<script>
import Axios from 'axios';

export default {
    name: 'in-process',
    props: {
        'dbData': Array,
        'todoData': Array,
    },
    data(){
      return {
        columns: [
        {
          key:"id",
          label:"id",
          sortable:true
        },
        {
          key:"owner_image",
          label:"owner"
        },
          "name",
          "todos"
        ],
        tableData: [],
        tableData_limited: [],
        todos_fields: [
          {
            key: 'todo_title',
            label: 'TODOS'
          }
        ],
        in_process_fields: [
          {
            key: 'card',
            label: ''
          },
        ]
      }
    },
    computed: {
      in_process() {
        return this.tableData = this.dbData.filter(function(data){ return data.status == "in_process" })
      },
      in_process_limited() {
        this.tableData_limited = this.dbData.filter(function(data){ return data.status == "in_process" })
        return this.tableData_limited.splice(0, 3)
      },
    },
    methods: {
      filter_todos(job_id) {
        return this.todoData.filter( data => { return data.job_id == job_id; })
      },
    }
}
</script>


<style scoped>
.card {
  width: 20rem;
  margin: 0 auto;
  transition: all 0.2s linear 0s;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.3);
  border: 1px solid gray;
}
.card{
  animation: animate-cards 0.8s ease;
}
@keyframes animate-cards {
  0% {
    transform: scale(0.5)
  }
  100% {
    transform: scale(1.0)
  }

}
.rank-badge{
  position: absolute;
  height: 17px;
}
div.row{
  text-align: center !important;
}
.avatar{
  width: 100px !important;
  height: 100px !important;
  box-shadow: 0px 0px 0px 2px white;
}
.inprocess-avatar {
  position: absolute;
  left: 1rem;
}

table.notebook{
  border-radius: 5px;
  box-shadow: -2px 2px 1px 0px white;

}
table.notebook th, table.notebook td{
  padding: 5px;
  border-bottom: solid 1px #dfdfdf;
}
.email-me{
  position: absolute;
  bottom: 0;
  right: 10px;
  margin: 1em;
}
.card-header {
    background-color: #344675 !important;
    height: 4rem;
}
.card-body {
  margin-top: 3rem;
}

.user-details {
    position: absolute;
    left: 8rem;
    text-align: left;
    top: 2.6rem;
    color: white;
    text-transform: uppercase;
}
</style>