<template>
<div class="wrapper">
    <div class="main-panel">

        <div class="content">
            <div class="row">

                <!-- Refresh Button -->
                <div class="col-md-12">
                    <b-button @click="refreshData()" size="sm" variant="info" class="pull-right"><i class="tim-icons icon-refresh-02"></i> Refresh</b-button>
                </div>
                <!-- Users Table -->
                <div class="col-md-7">
                     <b-card class="text-left" title="Users" sub-title="List of users">
                        <b-card-text>

                            <b-form-input placeholder="Search..." v-model="search_user"></b-form-input>

                            <b-table
                                :filter="search_user"
                                :busy="isBusy"
                                :items="users" 
                                :fields="columns"
                                hover
                                selectable
                                select-mode="single"
                                style="cursor:pointer"
                                @row-selected="onRowSelected"
                                :per-page="perPage"
                                :current-page="currentPage"
                                
                            >
                                <template v-slot:table-busy>
                                    <div class="text-center text-danger my-2">
                                    <b-spinner class="align-middle"></b-spinner>
                                    <strong>Loading...</strong>
                                    </div>
                                </template>
                            </b-table>

                            <b-pagination align="center" v-show="users" v-model="currentPage" :per-page="perPage" :total-rows="totalUsers"></b-pagination>

                        </b-card-text>
                    </b-card>
                </div>

                <!-- Profile Panel -->
                <div class="col-md-5">
                    <UserCard v-if="user" :user="user"/>
                </div>

            </div>
        </div>

    </div>
</div>
    
</template>

<script>
import { mapActions, mapGetters } from 'vuex';
import UserCard from '@/pages/Profile/UserCard.vue'


export default {
    components: {
        UserCard,
    },
    data() {
        return {
            search_user: '',
            perPage: 5,
            totalUsers: 0,
            currentPage: 1,
            isBusy: true,
            columns: [
                {
                    key: 'id',
                    label: 'ID'
                },
                {
                    key: 'fname',
                    label: 'Firstname'
                },
                {
                    key: 'lname',
                    label: 'Lastname'
                },
                {
                    key: 'lanid',
                    label: 'LAN ID'
                },
                {
                    key: 'roles',
                    label: 'Roles'
                },
            ],
            user: null,
            user_data: {
                lanid: null,
            },
        }
    },
    async created() {
        this.user_data.lanid = this.$session.get('lanid');

        await this.FETCH_ALL_USERS().then( res => { this.totalUsers = this.users.length; this.isBusy = false;} );
        await this.fetchUsers(this.user_data).then( res => { 
            this.isBusy = false;

            if(res.data.message) {
                setTimeout( () => {
                    this.$router.push('/');
                },1000);
            }
        });
        await this.check_admin();
    },
    computed: {
        ...mapGetters(['users'])
    },
    methods: {
        ...mapActions(['fetchUsers','FETCH_ALL_USERS']),

        onRowSelected(items) {
            items.forEach( (data, index) => {
                this.user = data;
            });
        },
        async refreshData() {
            this.isBusy = true;
            await this.FETCH_ALL_USERS().then(res => { this.isBusy = false });
            await this.fetchUsers().then(res => { this.isBusy = false });
        },
        check_admin() {
            let user = this.users.filter( data => data.id == this.$session.get('id'));

            user.forEach( data => {
                user = data;
            });

            if (user) {
                let user_roles = user.roles.split(',');

                // If not admin redirect to user dashboard
                if(user_roles.indexOf('role_Admin') == -1) {
                    this.$router.push('/user-dashboard')
                }
            }
        },
    }
}
</script>

