<template>

  <div class="wrapper">
    <div class="main-panel">

      <div class="content">
        <div class="row">

          <!-- Refresh Button -->
          <div class="col-md-12">
            <b-button @click="refreshData()" size="sm" variant="info" class="pull-right"><i class="tim-icons icon-refresh-02"></i> Refresh</b-button>
          </div>

          <!-- List of user's task -->
          <div class="col-md-12">

            <user-base-table
              title="Tasks"
              subtitle="LIST OF YOUR TASK"
              :data="my_jobs(allJobs)"
              :todos="GET_TODOS"
              :columns="columns"
              :isBusy="isBusy"
              btn_start
              btn_cancel
              btn_forApproval
              >
            </user-base-table>

          </div>

          <!-- List of job processing -->
          <div class="col-md-12">

            <user-base-table 
              title="Processing"
              subtitle="JOBS CURRENTLY PROCESSING"
              :data="my_process(allJobs)"
              :todos="GET_TODOS"
              :columns="columns"
              :isBusy="isBusy"
              btn_complete
              >
            </user-base-table>

          </div>
          
          <!-- list of completed jobs -->
          <div class="col-md-12">
            <user-base-table 
              title="Completed"
              subtitle="JOBS COMPLETED"
              :data="completed(allJobs)"
              :todos="GET_TODOS"
              :columns="columns"
              :isBusy="isBusy"
              >
            </user-base-table>
          </div>

        </div>
      </div>
    </div>
     
  </div>
</template>
<script>

import UserBaseTable from '@/layout/starter/Tables/UserBaseTable.vue'
import AdminBaseTable from '@/layout/starter/Tables/AdminBaseTable.vue'

import { mapActions, mapGetters } from 'vuex'

var randomNames = '';
var randomImages = '';

export default {
  name: 'user-dashboard',
  components: {
    UserBaseTable,
    AdminBaseTable,
  },
  data() {
    return {
      isBusy: true,
      perPage: 5,
      currentPage1: 1,
      currentPage2: 1,
      columns: [
        {
          key: 'todos',
          label: 'Todos',
        },
        {
          key: 'id',
          label: 'Ref #',
          sortable: true
        },
        {
          key:"owner_image",
          label:"Owner",
        },
        {
          key: 'name',
          label: 'Job Name',
          sortable: true
        },
        {
          key: 'projectName',
          label: 'Project Name',
          sortable: true
        },
        {
          key: 'n_size',
          label: 'Overall N-Size'
        },
        {
          key: 'num_of_resp',
          label: '# of Respondents'
        },
        {
          key: 'num_of_client_users',
          label: '# of Client Users'
        },
        {
          key: "status",
          label: "Status",
        },
        {
          key: "created_at",
          label: 'date created',
          sortable: true
        },
        "action"
        ],
      user_roles: [],
      user_data: {
        lanid: null
      }
    };
  },
  mounted: async function() {
    this.user_data.lanid = this.$session.get('lanid');

    await this.fetchUsers(this.user_data)
        .then( res => {
          if(res.data.message) {
              this.$router.push('/');
          }
        });

    await this.fetchJobs().then( () => this.isBusy = false);
    await this.FETCH_TODOS().then( () => this.isBusy = false);
  },
  computed: {    
    ...mapGetters(['allJobs','user', 'GET_TODOS']),
  },
  methods: {
    ...mapActions(['fetchJobs', 'fetchUsers', 'FETCH_TODOS']),

    async refreshData() {
      this.isBusy = true;

      await this.fetchJobs().then( () => this.isBusy = false );
      await this.FETCH_TODOS().then( () => this.isBusy = false );
    },

    // check_admin() {
    //   console.log(this.user)
    //   this.user_roles = this.user.roles.split(',');


    //   if(this.user_roles.indexOf('role_Admin') == 1) {
    //     this.showAdmin = true;
    //   }
      
    // },
    my_jobs: function(jobs) {
      if(jobs) {
        return jobs.filter( data => {
          return data.owner_id == this.user.id && (data.status != 'in_process' && data.status != 'completed')
        })
        
      }
    },
    in_queue: function(jobs){
      if(jobs) {
        return jobs.filter( data => {
          return data.status != "in_process"
        });
      }
    },
    completed: function(jobs){
      if(jobs) {
        return jobs.filter( data => {
          return data.owner_id == this.user.id && data.status == "completed"
        });
      }
    },
    my_process: function(jobs){
      if(jobs) {
        return jobs.filter( data => {
          return data.owner_id == this.user.id && data.status == "in_process"
        });
      }
    },

  }
};
</script>
