<template>
    <div>
        <!-- <img class="bg-image" src="img/bg5.jpg"> -->

        <!-- <div class="welcome">
            <div class="welcome-body">
                <h1>Welcome</h1>
                <b-form>
                    <label>Please enter your LAN ID</label>
                    <b-form-input></b-form-input>
                    <b-button type="submit" class="btn btn-primary" size="sm">LOGIN</b-button>
                </b-form>
            </div>

        </div> -->
        <div class="login-panel col-md-4 text-center">
            <b-card title="EES Job Manager">
                <b-form @submit.prevent="login()">
                    <label>Please enter your LAN ID</label>
                    <b-form-input v-model="user_data.lanid" required></b-form-input>
                    <b-button v-if="!loginLoading" type="submit" class="btn btn-primary" size="sm">LOGIN</b-button>
                    <b-button v-if="loginLoading" type="submit" class="btn btn-primary" size="sm" disabled>LOGGING IN...</b-button>
                </b-form>
            </b-card>
            <router-link to="/register">Not yet registered?</router-link>
        </div>

    </div>
    
</template>

<script>

import { mapGetters, mapActions } from 'vuex';

export default {
    data() {
        return {
            username: '',
            profile_image: '',
            loginLoading: false,
            user_data: {
                lanid: null,
            }
        }
    },
    async created() {
        this.fetchUsers({lanid: this.$session.get('lanid')})
            .then( res => {
                if(!res.data.message && this.$session.get('lanid')) {
                    this.$router.push('/user-dashboard');
                }
            })
    },
    methods: {
        ...mapActions([
            'fetchUsers',
            'LOGIN'
            ]),
        
        login() {
            this.loginLoading = true;

            this.LOGIN(this.user_data)
                .then( res => {
                    if(res.success) {
                        // set session
                        this.$session.set('id', res.additional_data.id)
                        this.$session.set('user', res.additional_data.fname)
                        this.$session.set('lanid', res.additional_data.lanid)
                        this.$session.set('email', res.additional_data.email)
                        this.$session.set('region', res.additional_data.region)
                        
                        // notify user
                        this.notification('success', res.success);

                        setTimeout( () => { this.$router.push('/user-dashboard')}, 2000);
                    } else {
                        // notify user
                        this.notification('danger', res.message);
                        console.log(res);
                        this.loginLoading = false;
                    }
                })
        },
        notification(type, msg) {
            this.$notify({
                type: type,
                message: msg
            });
        }
    },
    computed: {
        ...mapGetters(['user']),
    }
}
</script>

<style scoped>
.bg-image{
    width: 100%;
    border-radius: 0;
    height: 100vh;
    opacity: 0.3;

}
.container-fluid{
    margin: 0 !important;
    padding: 0 !important;

}
.avatar {
    border-radius: 50%;
}
.login-panel {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
</style>