<template>

  <div class="wrapper">
    <div class="main-panel">

      <div class="content">
        <div class="row">

          <!-- Refresh Button -->
          <div class="col-md-12">  
            <b-button @click="refreshData()" size="sm" variant="info" class="pull-right"><i class="tim-icons icon-refresh-02"></i> Refresh</b-button>
          </div>
          <!-- For Approval -->
          <div class="col-md-12">

            <admin-base-table
              title="For Approval"
              subtitle="List of jobs for approval"
              :data="for_approval(allJobs)"
              :todos="GET_TODOS"
              :columns="columns"
              btn_park
              btn_approve
              :isBusy="isBusy"
              >
            </admin-base-table>

          </div>

          <!-- In Process -->
          <div class="col-md-12">

            <admin-base-table
              title="In Process"
              subtitle="List of jobs in Queue"
              :data="in_progress_jobs(allJobs)"
              :todos="GET_TODOS"
              :columns="columns"
              btn_park
              btn_approval
              btn_complete
              search_field
              :isBusy="isBusy"
              >
              
            </admin-base-table>
          </div>

          <!-- In Queue -->
          <div class="col-md-12">

            <admin-base-table
              title="In Queue"
              subtitle="List of jobs in Queue"
              :data="in_queue_jobs(allJobs)"
              :todos="GET_TODOS"
              :columns="columns"
              btn_forApproval
              btn_approve
              :isBusy="isBusy"
              >
              
            </admin-base-table>

          </div>

          <!-- Approved Jobs -->
          <div class="col-md-12">

            <admin-base-table
              title="Approved"
              subtitle="List of approved Jobs"
              :data="approved_jobs(allJobs)"
              :todos="GET_TODOS"
              :columns="columns"
              :isBusy="isBusy"
              btn_park
              btn_complete
              >
            </admin-base-table>

          </div>

          <!-- Completed Jobs -->
          <div class="col-md-12">

            <admin-base-table
              title="Completed"
              subtitle="List of completed Jobs"
              :data="completed_jobs(allJobs)"
              :todos="GET_TODOS"
              :columns="columns"
              search_field
              :isBusy="isBusy"
              >
            </admin-base-table>

          </div>

        </div>
      </div>
    </div>
     
  </div>
</template>
<script>

import UserBaseTable from '@/layout/starter/Tables/UserBaseTable.vue'
import AdminBaseTable from '@/layout/starter/Tables/AdminBaseTable.vue'

import { mapActions, mapGetters } from 'vuex'

var randomNames = '';
var randomImages = '';

export default {
  name: 'user-dashboard',
  components: {
    UserBaseTable,
    AdminBaseTable,
  },
  data() {
    return {
      columns: [
        {
          key: 'todos',
          label: 'Todos'
        },
        {
          key: 'id',
          label: 'Ref #'
        },
        {
          key:"owner_image",
          label:"Owner",
        },
        {
          key: 'name',
          label: 'Job Name',
          sortable: true
        },
        {
          key: 'projectName',
          label: 'Project Name'
        },
        {
          key: 'n_size',
          label: 'Overall N-Size'
        },
        {
          key: 'num_of_resp',
          label: '# of Respondents'
        },
        {
          key: 'num_of_client_users',
          label: '# of Client Users'
        },
        {
          key: "created_at",
          label: 'date created'
        },
        'action'
        ],
      tableData: [],
      user_roles: [],
      isBusy: true,
    };
  },
  created: async function() {

    await this.fetchUsers({lanid: this.$session.get('lanid')});
    await this.fetchJobs().then( () => this.isBusy = false );
    await this.FETCH_TODOS().then( () => this.isBusy = false );
    await this.check_admin();

  },
  computed: {    
    ...mapGetters(['allJobs','user', 'GET_TODOS']),
  },
  methods: {
    ...mapActions(['fetchJobs', 'fetchUsers', 'FETCH_TODOS']),

    async refreshData() {
      this.isBusy = true;
      await this.fetchJobs().then( () => this.isBusy = false );
      await this.FETCH_TODOS().then( () => this.isBusy = false );
    },
    check_admin() {
      if (this.user) {
        this.user_roles = this.user.roles.split(',');

        // If not admin redirect to user dashboard
        if(this.user_roles.indexOf('role_Admin') == -1) {
          this.$router.push('/user-dashboard')
        }
      }

    },
    // filter all jobs for approval
    for_approval: function(jobs) {
      if(jobs) {
        return jobs.filter( data => {
          return data.status == "for_approval"
        })
      }
    },
    // filter all jobs in queue
    in_queue_jobs: function(jobs){
      if(jobs) {
        return jobs.filter( data => {
          return data.status == "in_queue"
        });
      }
    },
    // filter all jobs in process
    in_progress_jobs: function(jobs){
      if(jobs) {
        return jobs.filter( data => {
          return data.status == "in_process"
        });
      }
    },
    // filter approved jobs
    approved_jobs: function(jobs) {
      if(jobs) {
        return jobs.filter( data => {
          return data.status == "approved"
        });
      }
    },
    // filter completed jobs
    completed_jobs: function(jobs) {
      if (jobs) {
        return jobs.filter( data => {
          return data.status == 'completed'
        });
      }
    }

  }
};
</script>
