<template>
  <div class="wrapper">
    <side-bar>
      <template slot="links">
        <sidebar-link v-show="isAdmin" to="/admin-dashboard" :class="current_route('admin-dashboard')" name="Jobs" icon="tim-icons icon-bullet-list-67"/>

        <sidebar-link  v-show="isAdmin" to="" name="Manage" v-b-toggle.collapse-1 icon="tim-icons icon-key-25" />
        <b-collapse id="collapse-1" class="ml-3" :visible="manage_visible">
          <sidebar-link v-show="isAdmin" to="/manage-projects" :class="current_route('manage-projects')" name="Projects" icon="tim-icons icon-app"/>
          <sidebar-link v-show="isAdmin" to="/manage-users" :class="current_route('manage-users')" name="Users" icon="tim-icons icon-single-02"/>
          <sidebar-link v-show="isAdmin" to="/manage-worktypes" :class="current_route('manage-worktypes')" name="Work Types" icon="tim-icons icon-shape-star"/>
        </b-collapse>

        <sidebar-link to="/user-dashboard" :class="current_route('user-dashboard')" :name="$t('sidebar.dashboard')" icon="tim-icons icon-chart-pie-36"/>
        <sidebar-link to="/create-task" :class="current_route('create-task')" name="create task" icon="tim-icons icon-simple-add"/>

      </template>
    </side-bar>
    <div >
      <top-navbar></top-navbar>

      <dashboard-content @click.native="toggleSidebar">

      </dashboard-content>

      <content-footer></content-footer>
    </div>
  </div>
</template>

<script>
  import TopNavbar from "./SampleNavbar.vue";
  import ContentFooter from "./SampleFooter.vue";
  import DashboardContent from "./Content.vue";
  import MobileMenu from "./MobileMenu";
import { mapActions, mapGetters } from 'vuex';
  
  export default {
    data() {
      return {
        isAdmin: false,
        manage_visible: false,
        user_data: {
          lanid: null,
        },
      }
    },
    components: {
      TopNavbar,
      ContentFooter,
      DashboardContent,
      MobileMenu
    },
    created: function() {
      this.user_data.lanid = this.$session.get('lanid');
      this.fetchUsers(this.user_data)
                .then( () => {
                  this.check_role();
                });
    },
    computed: {
      ...mapGetters(['user'])
    },
    methods: {
      ...mapActions(['fetchUsers']),
      
      toggleSidebar() {
        if (this.$sidebar.showSidebar) {
          this.$sidebar.displaySidebar(false);
        }
      },
      check_role() {
        if (this.user) {
          this.user_roles = this.user.roles.split(',');

          if(this.user_roles.indexOf('role_Admin') > -1) {
            this.isAdmin = true;
          }

        }
      },
      current_route(name) {
        let route = this.$route.path.replace('/','');

        let admin_links = ['manage-users', 'manage-worktypes', 'manage-projects'];

        if(admin_links.indexOf(route) > -1) {
          this.manage_visible = true;
        } else {
          this.manage_visible = false;
        }

        if( route == name) {
          return 'active';
        }

        return '';
      }
    }
  };
</script>
<style lang="scss" scoped>
  .content{
    padding: 0px;
    overflow: hidden
  }
</style>
