<template>

    <div class="content">
        <div class="row">

            <!-- Registration Panel -->
            <div class="col-md-4 offset-md-4 mt-5">
            <b-card title="Registration">
                </b-form-group>
                <form @submit.prevent="register" method="post">

                    <!-- Firstname -->
                    <div class="form-row mb-1">
                        <div class="col">
                            <b-form-input type="text" v-model="form.fname" placeholder="Firstname" required/>
                        </div>
                    </div>
                    
                    <!-- Lastname -->
                    <div class="form-row mb-1">
                        <div class="col">
                            <b-form-input type="text" v-model="form.lname" placeholder="Lastname" required/>
                        </div>
                    </div>

                    <!-- LANID -->
                    <div class="form-row mb-1">
                        <div class="col">
                            <b-form-input type="text" v-model="form.lanid" placeholder="LANID" required/>
                        </div>
                    </div>

                    <!-- EMAIL -->
                    <div class="form-row mb-1">
                        <div class="col">
                            <b-form-input type="email" v-model="form.email" placeholder="Email" required/>
                        </div>
                    </div>

                    <!-- Region -->
                    <div class="form-row mb-1">
                        <div class="col">
                            <select class="form-control" v-model="form.region" name="region" required>
                                <option value="null" disabled selected>-- Please select region --</option>
                                <option class="text-default" v-for="(item, index) in regions" :key="index" :items="item" :value="item.value">
                                    {{ item.text }}
                                </option>
                            </select>
                        </div>
                    </div>

                    <!-- Profile Image -->
                    <div class="form-row">
                        <div class="col">
                            <label class="text-danger"><em>Please note: Max file size of 2M and allowed extensions (.jpg, .png)</em></label>
                            <b-form-file
                                size="sm"
                                placeholder="Choose profile image or drop it here"
                                accept=".jpg, .png"
                                @change="setImage($event)"
                                required
                            ></b-form-file>
                        </div>
                    </div>

                    <!-- Submit Button -->
                    <div class="form-row">
                        <div class="col">
                            <input v-if="!loadingRegister" type="submit" class="btn btn-round btn-block btn-primary btn-sm" value="Register">
                            <input v-if="loadingRegister" type="button" disabled class="btn btn-round btn-block btn-primary btn-sm" value="Registering...">
                        </div>
                    </div>

                </form>

            </b-card>
            </div>

        </div>
    </div>

</template>

<script>
import Axios from 'axios';
import { mapActions } from 'vuex';

export default {
    data() {
        return {
            loadingRegister: false,
            form: {
                fname: '',
                lname: '',
                lanid: '',
                email: '',
                region: null,
                image: 'img/default-avatar.png',
            },
            regions: [
                {value: 'APAC', text: 'APAC'},
                {value: 'EMEA', text: 'EMEA'},
                {value: 'north_america', text: 'North America'},
            ]
        }
    },
    created() {
        this.checkSession()

    },
    methods: {
        ...mapActions(['CREATE_USER']),

        checkSession() {
            if(this.$session.get('id') != undefined) {
                this.$router.push('/welcome')
            }
        },
        setImage(event) {
            this.form.image = event.target.files[0];
        },
        async register() {
            let data = new FormData();
            
            data.append('fname',this.form.fname);
            data.append('lname',this.form.lname);
            data.append('lanid',this.form.lanid);
            data.append('email',this.form.email);
            data.append('region',this.form.region);
            data.append('image',this.form.image);

            

            if(this.form.image.size > 2000000) {

                this.$notify({
                    title: 'Failed',
                    message: 'File size exceeds 2M limit.',
                    type: 'danger',
                    verticalAlign: 'top',
                    horizontalAlign: 'left',
                    
                });

            } else {
                this.loadingRegister = true;
                this.CREATE_USER(data)
                    .then( res => {
                        this.notify(res);
                        this.loadingRegister = false;
                    });
            }
            

        },
        notify(response) {
            if(!response.data.message) {

                this.$notify({
                    title: 'Success',
                    message: 'You are now registered!',
                    type: 'success',
                    verticalAlign: 'top',
                    horizontalAlign: 'left',
                    
                });

                setTimeout( () => {
                    this.$router.push('/');
                }, 3000);

                

            } else {

                this.$notify({
                    title: 'Error',
                    message: response.data.message,
                    type: 'danger',
                    verticalAlign: 'top',
                    horizontalAlign: 'left',
                    
                });
                
            }
        }
    },
}
</script>

<style scoped>
