<template>
    <div>
        <div>
            <card>
                <h4 class="title">{{ title }}</h4>
                <h6 class="card-subtitle text-muted">{{ subtitle }}</h6>

                <!-- search field -->
                <b-form-input v-if="search_field" v-model="filter" type="text" placeholder="Search job..." class="mt-3"/>

                <b-table :filter="filter" :busy="isBusy" hover :items="data" :fields="columns" :per-page="perPage" :current-page="currentPage">
                    
                    <template v-slot:cell(projectName)="data">
                        {{ data.item.project.name }}
                    </template>

                    <template v-slot:cell(todos)="row">
                        <b-button variant="secondary" size="sm" class="text-secondary" @click="row.toggleDetails">
                            {{ row.detailsShowing ? '-':'+'}}
                        </b-button>
                    </template>

                    <template v-slot:cell(owner_image)="data")>
                        <img :src="data.value" class="photo" v-b-popover.hover.right="user_info(data.item)"/>
                    </template>

                    <template v-slot:cell(action)="data">
                        <b-button title="For Approval" v-if="btn_forApproval" @click="updateStatus(data.item.id, 'for_approval', data.item)" class="btn btn-sm btn-warning"><span class="tim-icons icon-double-left"></span></b-button>
                        <b-button title="In Queue" v-if="btn_park" @click="updateStatus(data.item.id, 'in_queue', data.item)" class="btn btn-sm btn-warning"><span class="tim-icons icon-delivery-fast"></span></b-button>
                        <b-button title="Approve" v-if="btn_approve" @click="updateStatus(data.item.id, 'approved', data.item)" class="btn btn-sm btn-success"><span class="tim-icons icon-check-2"></span></b-button>
                        <b-button title="Complete Process" v-if="btn_complete" @click="updateStatus(data.item.id, 'completed', data.item)" class="btn btn-sm btn-success"><span class="tim-icons icon-check-2"></span></b-button>
                    </template>

                    <template v-slot:table-busy>
                        <div class="text-center text-danger my-2">
                        <b-spinner class="align-middle"></b-spinner>
                        <strong>Loading...</strong>
                        </div>
                    </template>

                    <template v-slot:row-details="row">
                        <b-card sub-title="List of todos">
                            <b-table
                                :items="filterTodos(row.item.id)"
                                :fields="todos_fields"
                                :current-page="todo_currentPage"
                                :per-page="todo_perPage"
                            >

                            </b-table>

                            <b-pagination
                                v-show="filterTodos(row.item.id).length > 3"
                                :per-page="todo_perPage"
                                v-model="todo_currentPage"
                                :total-rows="filterTodos(row.item.id).length"
                                align="center"
                            >
                            </b-pagination>

                        </b-card>
                    </template>
                </b-table>

            <b-pagination v-show="hasRecord && recordLength > 5" v-model="currentPage" :total-rows="recordLength" :per-page="perPage" align="center"></b-pagination>

            </card>
        </div>
    </div>
</template>

<script>
import { mapActions } from 'vuex';

export default {
    name: 'UserBasetable',
    props: {
        'title': String,
        'subtitle': String,
        'data': Array,
        'todos': Array,
        'columns': Array,
        'btn_park': Boolean,
        'btn_approve': Boolean,
        'btn_forApproval': Boolean,
        'btn_complete': Boolean,
        'search_field': Boolean,
        'isBusy': Boolean,
    },
    data() {
        return {
            perPage: 5,
            currentPage: 1,
            hasRecord: false,
            recordLength: 0,
            filter: null,
            todos_fields: [
                {
                    key: 'todo_title',
                    label: 'name'
                }
            ],
            dummy_todos: [
                {
                    "id": "1",
                    "name": "Dummy",
                    "nodes": "0",
                    "n_size": "0",
                    "num_of_resp": "0",
                    "num_of_client_users": "0",
                    "status": "dummy_status",
                    "owner_id": "0",
                    "owner_name": "Niel Malitoc",
                    "owner_image": "http://localhost/ejm/api/images/default-avatar.png",
                    "owner_roles": "role_User,role_Admin",
                    "created_at": "Mon Jan. 20, 2020 09:01 PM"
                }
            ],
            todo_perPage: 3,
            todo_currentPage: 1,
            mail: {
                to: '',
                from: 'EJM.Notification@willistowerswatson.com',
                subject: '',
                message: '',
            }
        }
    },
    created() {
        this.getRecordLength();
    },
    methods: {
        ...mapActions(['fetchJobs','updateTask','SEND_MAIL']),

        getRecordLength() {
            if(this.data) {
                this.hasRecord = true;
                this.recordLength = this.data.length;
            }
        },
        async updateStatus(id, status, data) {

            this.mail_template(data, status);

            await this.updateTask({id: id, status: status})
                .then( res => {
                    
                   if(res.success) {

                    this.SEND_MAIL(this.mail)
                        .then( res => {
                            if(res.success) {
                                this.notify('info', 'Email notification is now sent to owner')
                            } else {
                                this.notify('danger', 'Email notification failed to send.')
                                console.log(res);
                            }
                    })
                    
                    this.fetchJobs();

                   }

                });

        },
        filterTodos (job_id) {
            if(this.todos) {
                return this.todos.filter( data => data.job_id == job_id);
            } else {
                return this.dummy_todos.filter( data => data.job_id == job_id);
            }
        },
        user_info(item) {
            let info = item.owner_name+"\n";
            info += item.owner_roles;
            return info;
        },
        notify(type, msg) {
            this.$notify({
                verticalAlign: 'top',
                horizontalAlign: 'right',
                type: type,
                message: msg
            })
        },
        mail_template(data, status) {
            this.mail.to = data.owner_email;
            this.mail.subject = "EJM_Notification - TaskID: "+ data.project.id +" - "+status.replace("_", " ").toUpperCase();
            this.mail.message = "<b>Project name:</b> "+data.project.name+"<br/>"+
                                "<b>N-size:</b> "+data.n_size+"<br/>"+
                                "<b>Number of Respondents/Participants/Nodes:</b> "+data.num_of_resp+"<br/>"+
                                "<b>Number of client users:</b> "+data.num_of_client_users+"<br/>";
            if(status == "approved") {
                this.mail.message += "<b>Approved by:</b> "+this.$session.get('email');
            }
        }
    }
    
}
</script>

